import React, { useState, useEffect } from 'react';
import E from 'wangeditor';
import sty from "./quill.module.less";

let editor: any;

const Quill = () => {
	const [content, setContent] = useState('')

	useEffect(() => {
		editor = new E("#div1");
		// ()
		// editor.config = {
		// 	uploadImgServer: 'http://172.22.51.149:8080/cpmApi//project/file/upload',
		// 	uploadImgMaxSize: 50 * 1024 * 1024, // 2M
		// 	uploadImgMaxLength: 5, //  5 
		// 	uploadImgParams: {
		// 		token: 'xxxxx'
		// 	}
		// };
		editor.config.onchange = () => (newHtml: any) => {
			setContent(newHtml);
		};
		/** */
		editor.create()
		return () => {
			//   classcomponentWillUnmount
			editor.destroy()
		}
	}, [])

	// html1
	const getHtml = () => {
		alert(content)
	}

	// html2
	const getHtml1 = () => {
		alert(editor.txt.html())
	}

	// text
	const getText = () => {
		alert(editor.txt.text())
	}

	return (
		<div className={sty.quill}>
			<div id="div1"></div>
			<button onClick={getHtml}>html</button>
			<button onClick={getHtml1}>html1</button>
			<button onClick={getText}>text</button>
		</div>
	);
};

export default Quill;
